import React, {useRef, useState} from 'react'
import { getAuth, sendPasswordResetEmail } from "firebase/auth";
import "../../css/Compte.css";
export default function ResetPwd() {

const auth = getAuth();


const [validation, setValidation] = useState("")

const inputs =  useRef([])
const addInputs = el => {
    if(el && !inputs.current.includes(el)){
        inputs.current.push(el)
    }
}

const formRef = useRef();
const handleForm = async (e) => {
  
const ResetMotDePasse = document.getElementById('monMail').value;
  e.preventDefault()


  try{
      await sendPasswordResetEmail(auth, ResetMotDePasse)
        .then(() => {
            setValidation("Si le mail existe vous avez reçu un mail")
        }).catch((error) => {
          const errorCode = error.code;
          const errorMessage = error.message;
          // ..
        });
}catch (err){
      
      
}
}


  return (
    <div className='blockCentre'>
      
      
    <div className='contenuBlock'>
        <h1>Information de mon compte</h1>
        <form  ref={formRef} onSubmit={handleForm} className='sign-up-form'>
        <div className='inputStyle'> 
        <label className='form-label'  htmlFor='Mail'>Adresse mail</label>
          <input 
                    ref={addInputs}
                  className='inputDesign'
                  
                  type="mail"
                  name="mail" 
                  id="monMail"/>
        </div>
        <br></br>
        
        
        
       
                  <p className='text-danger mt-1'>{validation}</p>
          <button className='btnMot'>
              Enregistrer
          </button>
        </form>
       
    </div></div>
  )
}
